.estimation {
	color: $white;
	@include helvetica;

	&__container {}

	&__header {
		background-color: $black;
		padding: 70px 0; }

	&__title {}

	&__descr {
		max-width: 775px;
		font-size: 14px;
		line-height: 1.85; }

	&__list {}

	&__item {
		position: relative;
		&:not(:first-child) {
			&:after {
				content: '';
				position: absolute;
				width: 100%;
				height: 1px;
				top: 0;
				left: 0;
				background-color: #e6e6e6; } } } }



